"use client";

import { motion } from "framer-motion";
import { Video, Share2, Palette, ArrowUpRight } from "lucide-react";
import { useLanguage } from "@/lib/LanguageContext";

export default function Services() {
    const { t } = useLanguage();

    const services = [
        {
            title: t.services.s1_title,
            description: t.services.s1_desc,
            icon: <Video className="w-8 h-8" />,
            color: "from-purple-500/20 to-blue-500/0",
        },
        {
            title: t.services.s2_title,
            description: t.services.s2_desc,
            icon: <Share2 className="w-8 h-8" />,
            color: "from-orange-500/20 to-red-500/0",
        },
        {
            title: t.services.s3_title,
            description: t.services.s3_desc,
            icon: <Palette className="w-8 h-8" />,
            color: "from-green-500/20 to-emerald-500/0",
        },
    ];

    return (
        <section id="services" className="py-32 px-6 bg-black text-white overflow-hidden">
            <div className="max-w-7xl mx-auto">
                <div className="mb-24 flex flex-col md:flex-row items-end justify-between gap-8">
                    <div className="max-w-2xl">
                        <motion.span
                            initial={{ opacity: 0, x: -20 }}
                            whileInView={{ opacity: 1, x: 0 }}
                            className="text-gray-500 font-mono tracking-widest uppercase mb-4 block"
                        >
                            {t.services.tagline}
                        </motion.span>
                        <motion.h2
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            className="text-5xl md:text-7xl font-syne font-bold leading-tight uppercase"
                        >
                            {t.services.title_start} <span className="text-gray-500">{t.services.title_mid}</span> <br />
                            {t.services.title_end}
                        </motion.h2>
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-1px bg-white/10 border border-white/10 rounded-2xl overflow-hidden">
                    {services.map((service, index) => (
                        <motion.div
                            key={index}
                            initial={{ opacity: 0 }}
                            whileInView={{ opacity: 1 }}
                            transition={{ delay: index * 0.1 }}
                            whileHover={{ backgroundColor: "rgba(255, 255, 255, 0.05)" }}
                            className="relative p-12 bg-black transition-colors group"
                        >
                            {/* Subtle Gradient Background on Hover */}
                            <div className={`absolute inset-0 bg-gradient-to-br ${service.color} opacity-0 group-hover:opacity-100 transition-opacity duration-500`} />

                            <div className="relative z-10">
                                <div className="mb-8 text-white p-3 border border-white/10 w-fit rounded-lg bg-white/5">
                                    {service.icon}
                                </div>
                                <h3 className="text-2xl font-syne font-bold mb-4 flex items-center gap-2">
                                    {service.title}
                                    <ArrowUpRight className="w-5 h-5 opacity-0 group-hover:opacity-100 group-hover:translate-x-1 group-hover:-translate-y-1 transition-all" />
                                </h3>
                                <p className="text-gray-400 leading-relaxed font-light text-lg">
                                    {service.description}
                                </p>
                            </div>

                            {/* Number decoration */}
                            <span className="absolute bottom-8 right-8 text-8xl font-black text-white/5 font-syne pointer-events-none group-hover:text-white/10 transition-colors">
                                0{index + 1}
                            </span>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
}
